import React, { Component } from 'react';
import {Tabs, Tab} from 'material-ui/Tabs';
import {GridList, GridTile} from 'material-ui/GridList';
import Paper from 'material-ui/Paper';

import CircularProgress from 'material-ui/CircularProgress';
import injectTapEventPlugin from 'react-tap-event-plugin';
injectTapEventPlugin();

import async from 'async';

import Pokedex from '../Pokedex';
import PokemonCard from './PokemonCard';



class HomePage extends React.Component {
  constructor(props)
  {
    super(props);
    this.state = {pokemonList: Array.from(Array(150).keys())};

    Pokedex.getPokemonsList().then(ress => {
      return ress.results;
    }).then(pokemons => {

      async.forEachOf(pokemons, (pokemon) => {
        Pokedex.getPokemonByName(pokemon.name, pokemon => {
          this.state.pokemonList.push(pokemon);
          this.setState({pokemonList: this.state.pokemonList});
        });
      });
    }).catch(err => {
      console.log(err);
    });
  }

  renderPokemons()
  {
    return this.state.pokemonList.map(pokemon => {
      if(pokemon.name)
      {
        return(
          <GridTile key={pokemon.name} title={pokemon.name} subtitle={<span>{pokemon.types.map(type => {return type.type + " ";})}</span>}>
            <img role="presentation" src={pokemon.sprites.front_default}/>
          </GridTile>
        );
      }else{
        return(<CircularProgress key={pokemon} size={1.5} />);
      }
    });
  }

  render(){
    const styles = {
      root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
      },
      gridList: {
        width: 500,
        height: 500,
        overflowY: 'auto',
        marginBottom: 24,
      },
    };

    const paperStyle = {
      height: 1000,
      width: 3000,
      margin: 20,
      textAlign: 'center',
      display: 'inline-block',
    };

    return(
      <Tabs>
        <Tab label="PokeDex">
          <Paper style={paperStyle} zDepth={1} rounded={false} >
            <div style={styles.root}>
              <GridList cellHeight={200} style={styles.gridList}>
                {this.renderPokemons()}
              </GridList>
            </div>
          </Paper>
        </Tab>
        <Tab label="PokeWorld">
          <div>
            <h1>There should be a map beneath me</h1>
            <div id="map"></div>
          </div>
        </Tab>
      </Tabs>
    );
  }

};

export default HomePage;



